import { getCameraById } from "@/lib/data";
import { notFound } from "next/navigation";
import Image from "next/image";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import FavoriteButton from "@/components/favorite-button";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { ArrowLeft, Zap, TrafficCone, CheckCircle2, AlertTriangle, Info } from "lucide-react";
import { Skeleton } from "@/components/ui/skeleton";
import CameraMap from "@/components/camera-map";
import { cn } from "@/lib/utils";

type Props = {
    params: { id: string };
};

export async function generateMetadata({ params }: Props) {
    const camera = getCameraById(params.id);
    if (!camera) {
        return { title: 'Camera Not Found' };
    }
    return {
        title: `${camera.location} | Kiwi Traffic Watch`,
        description: `Live traffic camera at ${camera.location}`,
    };
}

const statusIcons = {
    Active: <CheckCircle2 className="h-4 w-4 text-green-500" />,
    'Under Maintenance': <TrafficCone className="h-4 w-4 text-orange-500" />,
    Proposed: <Info className="h-4 w-4 text-blue-500" />,
};

const typeIcons = {
    'Fixed': <Zap className="h-4 w-4" />,
    'Red Light': <AlertTriangle className="h-4 w-4" />,
    'Average Speed': <Zap className="h-4 w-4" />,
    'Mobile Site': <Zap className="h-4 w-4" />,
}


export default function CameraPage({ params }: Props) {
    const camera = getCameraById(params.id);

    if (!camera) {
        notFound();
    }

    return (
        <div className="container mx-auto max-w-5xl py-8 px-4">
            <Button asChild variant="ghost" className="mb-6">
                <Link href="/">
                    <ArrowLeft className="mr-2 h-4 w-4" />
                    Back to all cameras
                </Link>
            </Button>
            <div className="grid lg:grid-cols-2 gap-8">
                <div className="space-y-6">
                    <Card>
                        <CardHeader className="flex-row items-start justify-between">
                             <div className="space-y-2">
                                <CardTitle className="text-2xl">{camera.location}</CardTitle>
                                <CardDescription>{camera.region}</CardDescription>
                            </div>
                            <FavoriteButton id={camera.id} />
                        </CardHeader>
                        <CardContent className="grid grid-cols-2 gap-4 text-sm">
                            <div className="flex items-center gap-2 p-3 bg-muted/50 rounded-lg">
                                <span className="font-bold text-2xl text-primary">{camera.speedLimit}</span>
                                <span className="text-muted-foreground">km/h</span>
                            </div>
                            <div className={cn("flex items-center gap-2 p-3 rounded-lg", 
                                camera.status === 'Active' && 'bg-green-100 dark:bg-green-900/30',
                                camera.status === 'Under Maintenance' && 'bg-orange-100 dark:bg-orange-900/30',
                                camera.status === 'Proposed' && 'bg-blue-100 dark:bg-blue-900/30'
                            )}>
                                {statusIcons[camera.status]}
                                <span className="font-semibold">{camera.status}</span>
                            </div>
                             <div className="flex items-center gap-2 p-3 bg-muted/50 rounded-lg col-span-2">
                                {typeIcons[camera.type]}
                                <span className="font-semibold">{camera.type} Camera</span>
                                <span className="text-muted-foreground ml-auto text-xs">Source: {camera.dataSource}</span>
                            </div>
                        </CardContent>
                    </Card>

                    <div className="aspect-video w-full relative overflow-hidden rounded-lg shadow-lg">
                         <Skeleton className="absolute inset-0" />
                        <Image 
                            src={camera.imageUrl} 
                            alt={`Live feed from ${camera.location}`} 
                            fill
                            className="object-cover transition-opacity duration-500"
                            sizes="(max-width: 1024px) 100vw, 50vw"
                            data-ai-hint={camera.imageHint}
                            priority
                        />
                    </div>
                </div>

                <CameraMap camera={camera} />
            </div>
        </div>
    );
}
